import React, {useEffect , useReducer, useState } from 'react';
import Axios from 'axios';
import {Navbar, Nav, Form, FormControl,Button} from 'react-bootstrap';
import App from '../../App';
import Login from '../Password/login';
import {Cookies} from 'react-cookie'
import LocalStorage from 'use-local-storage'


export default function MyNavbar() {
    const [getPassword , setGetPassword] = useState([]);
    useEffect(() => {
        Axios.get('/password')
            .then(res => {

                setGetPassword(res.data)

            })
            .catch(err => {
                console.log(err);
            })
    },[getPassword])
    const isLoaded = () => {
        window.scroll(0, 0);
    }
    function Logout(){
       alert('log out')

    }
    if(getPassword == 'undefined'){
        setGetPassword('nimrod')
    }
    console.log('getPassword',getPassword)
        return (
            <div >
           
                 
                <>
            
                    <Navbar bg="light" variant="light">
                        <Navbar.Brand href="/">Home Page</Navbar.Brand>
                        <Nav className="mr-auto">
                            <Nav.Link href="/GetUsers">Get Users</Nav.Link>
                            <Nav.Link href="/CreactUser">Creat User</Nav.Link>
                            <Nav.Link href="/getpassword">Get Password</Nav.Link>
                            <Nav.Link href="/CreatPassword">Sign up</Nav.Link>
                            <Nav.Link href="/Login">Log In</Nav.Link>
                        </Nav>
                        <Form inline>
                        <Button variant="outline-primary">{getPassword.length < 1? <a href='/Login'>Please Log In  </a> :  <> <> Well Come Back - {getPassword} - </> <Button variant="dark" onClick={Logout}> Log out </Button> </>} </Button>
                         
                            
                    

                        </Form>
                    </Navbar>
                </>
            </div>
        )
        
        
    
    
}